.App{
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    display: grid;
    
  position: relative;
  overflow: hidden;
}

Canvas{
    z-index: 1;
    height: 100vh;
    width: 100%;
    position: relative;
    
}

.navigation{
    z-index: 2;
    position: absolute;
    text-align: center;
    background-color: white;
    width: auto;
    padding: 0.5rem;
    
}

.reference{
    z-index: 2;
    position: absolute;
    bottom:0;
    right: 0;
    padding: 0.5rem;
    margin: 0.5rem;
    background-color: rgba(255, 255, 255, 0.6);;
}